
































/* Start Body */
body[data-v-5bcee7a1] {
  box-sizing: border-box;
  text-align: center;
  font: 16px;
 position: relative;
}


/*tooltip Box*/
.con-tooltip[data-v-5bcee7a1] {
  position: relative;
  background: #9575CD;

  border-radius: 9px;
  padding: 0 20px;
  margin: 10px;

  display: inline-block;
  color: white;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
/*tooltip */
.tooltip[data-v-5bcee7a1] {
  visibility: hidden;

  opacity: .40;
  width: 100%;
  padding: 0px 1px;
  background: white;
  position: absolute;
  border-radius: 9px;
  font: 16px;
  transform: translateY(9px);
  transition: all 0.3s ease-in-out;
  color: #9575CD;
  border: dotted #9575CD;
  margin: 10px;
}
.tooltip[data-v-5bcee7a1]::after {
  content: " ";
  width: 0;
  height: 0;

  border-style: solid;
  border-width: 12px 12.5px 0 12.5px;
 /* border-color:  #337ab7 transparent transparent transparent;*/
  border-color:  #9575CD transparent transparent transparent;

  position: absolute;
}
.con-tooltip:hover .tooltip[data-v-5bcee7a1]{
  visibility: visible;
  transform: translateY(-10px);
  opacity: 1;
    transition: .3s linear;
  animation: odsoky-data-v-5bcee7a1 1s ease-in-out infinite alternate;
  margin: 10px;
  position: absolute;
}
@keyframes odsoky-data-v-5bcee7a1 {
0%{
    transform: translateY(6px);
}
100%{
    transform: translateY(1px);
}
}
.bottom[data-v-5bcee7a1]:hover {
  transform: translateY(6px);
  z-index: 100;
  position: relative;
}
.bottom .tooltip[data-v-5bcee7a1] {
  top:115%; left:-5%;
}
.bottom .tooltip[data-v-5bcee7a1]::after{
  top:-17%;
  left:40%;
}



